<template>
	<li class="stars-list" style="float: left; list-style: none; position: relative; width: 140px;">
		<a href="javascript:void(0);">
			<div class="pd-stars">
				<div class="front">
					<img :src="item.src" />
				</div>
				<div class="back">
					<img :src="item.src" />
					<div class="cover">
					</div>
					<div class="stars-info">
						<div class="intro">
							{{item.title}}
						</div>
						<div class="number">
							{{item.num}}
						</div>
					</div>
				</div>
			</div>
			<div class="thumb-title" :title="item.name">
				{{item.name}}
			</div>
		</a>
	</li>
</template>

<script>
	export default {
		props: ['item'],
	}
</script>

<style>

</style>


